import { useMemo, useState, useCallback, useEffect } from 'react';
import type { MaiMaiMood, UseMaiMaiMoodOptions } from './types';

/**
 * MaiMai å¿ƒæƒ…ç‹€æ…‹æ©Ÿ Hook
 * @description æ ¹æ“šå¤šç¨®è¼¸å…¥è¨ˆç®—ç•¶å‰æ‡‰é¡¯ç¤ºçš„å¿ƒæƒ…ç‹€æ…‹
 * 
 * å„ªå…ˆç´šé †åºï¼ˆé«˜åˆ°ä½ï¼‰ï¼š
 * 1. å¤–éƒ¨å¼·åˆ¶æŒ‡å®š (externalMood)
 * 2. é»æ“Šæ…¶ç¥ (isCelebrating)
 * 3. æˆåŠŸç‹€æ…‹ (isSuccess)
 * 4. éŒ¯èª¤ç‹€æ…‹ (hasError)
 * 5. è¼‰å…¥ä¸­ (isLoading)
 * 6. è¼¸å…¥å¯†ç¢¼ (isTypingPassword)
 * 7. è¼¸å…¥ email (isTypingEmail)
 * 8. hover ç‹€æ…‹ (isHovered)
 * 9. é è¨­ idle
 */
export function useMaiMaiMood(options: UseMaiMaiMoodOptions = {}): {
  mood: MaiMaiMood;
  clickCount: number;
  handleClick: () => void;
  resetCelebration: () => void;
} {
  const {
    externalMood,
    isSuccess = false,
    hasError = false,
    isLoading = false,
    isTypingPassword = false,
    isTypingEmail = false,
    isHovered = false,
    isCelebrating: externalCelebrating,
  } = options;

  const [clickCount, setClickCount] = useState(0);
  const [internalCelebrating, setInternalCelebrating] = useState(false);

  // é»æ“Š 5 æ¬¡è§¸ç™¼æ…¶ç¥
  const handleClick = useCallback(() => {
    setClickCount(prev => {
      const newCount = prev + 1;
      if (newCount >= 5) {
        setInternalCelebrating(true);
        // 2 ç§’å¾Œé‡ç½®
        setTimeout(() => {
          setInternalCelebrating(false);
          setClickCount(0);
        }, 2000);
      }
      return newCount;
    });
  }, []);

  const resetCelebration = useCallback(() => {
    setInternalCelebrating(false);
    setClickCount(0);
  }, []);

  // ä½¿ç”¨ useMemo è¨ˆç®— moodï¼Œé¿å…é–ƒçˆ
  const mood = useMemo<MaiMaiMood>(() => {
    // å„ªå…ˆç´š 1: å¤–éƒ¨å¼·åˆ¶æŒ‡å®š
    if (externalMood) return externalMood;
    
    // å„ªå…ˆç´š 2: æ…¶ç¥ç‹€æ…‹ï¼ˆé»æ“Šæˆ–å¤–éƒ¨è§¸ç™¼ï¼‰
    if (internalCelebrating || externalCelebrating) return 'celebrate';
    
    // å„ªå…ˆç´š 3: æˆåŠŸ
    if (isSuccess) return 'celebrate';
    
    // å„ªå…ˆç´š 4: éŒ¯èª¤
    if (hasError) return 'shy';
    
    // å„ªå…ˆç´š 5: è¼‰å…¥ä¸­
    if (isLoading) return 'thinking';
    
    // å„ªå…ˆç´š 6: è¼¸å…¥å¯†ç¢¼
    if (isTypingPassword) return 'peek';
    
    // å„ªå…ˆç´š 7: è¼¸å…¥ email
    if (isTypingEmail) return 'happy';
    
    // å„ªå…ˆç´š 8: hover
    if (isHovered) return 'wave';
    
    // é è¨­
    return 'idle';
  }, [
    externalMood,
    internalCelebrating,
    externalCelebrating,
    isSuccess,
    hasError,
    isLoading,
    isTypingPassword,
    isTypingEmail,
    isHovered,
  ]);

  return {
    mood,
    clickCount,
    handleClick,
    resetCelebration,
  };
}

/**
 * ç›£è½å…¨åŸŸ mascot:celebrate äº‹ä»¶çš„ Hook
 */
export function useMascotCelebrateEvent(onCelebrate: () => void) {
  useEffect(() => {
    const handler = () => onCelebrate();
    window.addEventListener('mascot:celebrate', handler);
    return () => window.removeEventListener('mascot:celebrate', handler);
  }, [onCelebrate]);
}
